@import "global";

#page{
  padding-top: 40px;
  top:30px;
  width: 100vw;
  left: 100vw;
  height: 98vh;
  margin-bottom:100px;
  overflow: scroll;
  position: fixed;
  background-color: #ffffff;
  -webkit-transition: all 1s ease;
  transition: all 1s ease;

  .back{
    background-color: $technogi-orange; 
    width: 100%;
    height:53px;
    position:fixed;
    top:10px;
    line-height:58px;
    padding-left:20px;
    color:white;
    cursor:pointer;
    font-size:.9em;
    z-index:10;
  }
}



#page.show {
  -ms-transform: translate(-100vw, 0px);
  -webkit-transform: translate(-100vw, 0px);
  transform: translate(-100vw, 0px);
  

}




.subpage_info{
  width:100%;
  display:none;
  .row{
    min-height:400px;
    margin:0;
    padding:0;
    width:100%;
    max-width:10000px;
    padding-bottom:70px;
    h1{
      text-align: center;
      font-size: 2.5em;
      font-weight:700;
      padding-top:70px;
      margin-bottom:40px;
    }
    p{
      font-size:1.3em;
      padding: 10px 100px 10px 100px;
      line-height:1.5em;
      font-weight: 500;
      text-align: center;
    }
    .list-item{
      margin-top:50px;
      margin-bottom:20px;
      .icon{
        font-size:3em;
        padding:20px;
        height:100px;
        width:100px;
        display:block;
        text-align: center;
        border:solid 3px white;
        border-radius:50px;
        line-height: 58px;     
      }
      .text{
        padding-top:50px;
        text-align: center;
        font-size:1.3em;
        min-height:200px;
        padding:20px;
      }   
    }
    .explicative-item{
      border-radius:3px;
      min-height:250px;
      height:250px;
      h1{
        padding:5px;
        padding-top:40px;
        margin-bottom:10px;
        font-size:1.2em;
      }
      p{
        font-size:1.1em;
        padding:10px;
      }
    }
    li{
      //padding-left:50px;
      font-size:1.5em;
      text-align: center;
      line-height: 2.5em;
    }
  }
  .row.image{
    min-height:400px;
    .columns{
      padding:0;
    }
    max-height:400px;
    overflow:hidden;
    img{
      position:absolute;
      top:0;left:0;
      filter:blur(4px);
      width:100%;
      z-index:0;
    }
    h1{
      span{
        padding-bottom:10px;
      }
      position:relative;
      padding-top:60px;
      font-size:4em;
      font-weight:700;
      color:rgba(240,240,240,.8);
      z-index:1;
    }
    h2{
      position:relative;
      margin:100px;
      padding:20px;
      color:transparentize(#f1f1f1,.1);
      text-align: center;
      font-size:1.5em;
      font-weight:400;
      background-color:transparentize(white,.7);
      border-radius:5px;
      //color:rgba(240,240,240,.5);
    }
  }


  .row.orange{
    background-color:$technogi-orange;
  }

  .row.yellow{
    background-color:$technogi-yellow;
  }

  .row.red{
    background-color:$technogi-red;
    h1{
      color:white;
    }
    p{
      color:white;
    }
    .list-item{
      .icon{
        color:white;
      }
      .text{
        color:white;
      }
    }
  }
  .row.blue{
    background-color:$technogi-blue;
  }
  .row.white{
    h1{
      color:$technogi-orange;
    }
    p{
      color:$gray;
    }
    li{
      color:$gray;
    }
    .list-item{
      .icon{
        border-color:$technogi-orange;
        color:$technogi-orange;
      }
      .text{
        color:$gray;
      }
    }
  }

  .row.gray{
    background-color:#eee;
    h1{
      color:$technogi-orange;
    }
    p{
      color:$gray;
    }
    .explicative-item{
      border:solid thin white;
      background-color:$technogi-orange;
      h1{
        color:white;
      }
      p{
        color:white;
      }
    }
  }
}